"use client";

import React, { useEffect, useState } from "react";
import { useAuth } from "@/hooks/useAuth";

interface Notice {
  id: number;
  title: string;
  content: string;
  author: string;
  priority: "LOW" | "MEDIUM" | "HIGH" | "URGENT";
  targetRoles: string[];
  isActive: boolean;
  expiryDate?: string;
  createdAt: string;
}

const NoticesPage: React.FC = () => {
  const { user } = useAuth();
  const [notices, setNotices] = useState<Notice[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedPriority, setSelectedPriority] = useState<string>("ALL");
  const [searchTerm, setSearchTerm] = useState("");

  useEffect(() => {
    const fetchNotices = async () => {
      try {
        // Sample notices data
        const sampleNotices: Notice[] = [
          {
            id: 1,
            title: "Mid-Term Examination Schedule Released",
            content: "The mid-term examination schedule for all classes has been released. Please check your respective class timetables and prepare accordingly. Examinations will begin from March 15th, 2024.",
            author: "Academic Office",
            priority: "HIGH",
            targetRoles: ["STUDENT", "TEACHER", "PARENT"],
            isActive: true,
            expiryDate: "2024-03-20",
            createdAt: "2024-01-20"
          },
          {
            id: 2,
            title: "Library Hours Extended",
            content: "Due to upcoming examinations, the library hours have been extended. The library will now be open from 7:00 AM to 10:00 PM on weekdays and 8:00 AM to 8:00 PM on weekends.",
            author: "Library Department",
            priority: "MEDIUM",
            targetRoles: ["STUDENT", "TEACHER", "STAFF"],
            isActive: true,
            createdAt: "2024-01-18"
          },
          {
            id: 3,
            title: "URGENT: Fee Payment Deadline",
            content: "This is a reminder that the fee payment deadline for the current semester is January 31st, 2024. Late payment will incur additional charges. Please ensure timely payment to avoid any inconvenience.",
            author: "Accounts Department",
            priority: "URGENT",
            targetRoles: ["STUDENT", "PARENT"],
            isActive: true,
            expiryDate: "2024-01-31",
            createdAt: "2024-01-15"
          },
          {
            id: 4,
            title: "New Computer Lab Inauguration",
            content: "We are pleased to announce the inauguration of our new state-of-the-art computer laboratory. The lab is equipped with the latest hardware and software to enhance the learning experience of our students.",
            author: "IT Department",
            priority: "MEDIUM",
            targetRoles: ["STUDENT", "TEACHER", "STAFF"],
            isActive: true,
            createdAt: "2024-01-12"
          },
          {
            id: 5,
            title: "Sports Day Registration Open",
            content: "Registration for the annual sports day is now open. Students can register for various events including athletics, basketball, football, and more. Registration deadline is February 10th, 2024.",
            author: "Sports Department",
            priority: "MEDIUM",
            targetRoles: ["STUDENT"],
            isActive: true,
            expiryDate: "2024-02-10",
            createdAt: "2024-01-10"
          },
          {
            id: 6,
            title: "Parent-Teacher Meeting",
            content: "A parent-teacher meeting has been scheduled for February 5th, 2024, from 10:00 AM to 4:00 PM. Parents are requested to attend and discuss their child's academic progress with respective teachers.",
            author: "Academic Office",
            priority: "HIGH",
            targetRoles: ["PARENT", "TEACHER"],
            isActive: true,
            expiryDate: "2024-02-05",
            createdAt: "2024-01-08"
          },
          {
            id: 7,
            title: "Scholarship Applications Available",
            content: "Merit-based scholarship applications for the next academic year are now available. Eligible students can apply through the student portal. Application deadline is March 1st, 2024.",
            author: "Student Affairs",
            priority: "MEDIUM",
            targetRoles: ["STUDENT", "PARENT"],
            isActive: true,
            expiryDate: "2024-03-01",
            createdAt: "2024-01-05"
          },
          {
            id: 8,
            title: "Campus WiFi Maintenance",
            content: "The campus WiFi network will undergo maintenance on January 25th, 2024, from 2:00 AM to 6:00 AM. Internet services may be temporarily unavailable during this period.",
            author: "IT Department",
            priority: "LOW",
            targetRoles: ["STUDENT", "TEACHER", "STAFF"],
            isActive: true,
            createdAt: "2024-01-03"
          }
        ];

        // Filter notices based on user role
        const filteredNotices = sampleNotices.filter(notice => 
          user && notice.targetRoles.includes(user.role)
        );

        setNotices(filteredNotices);
      } catch (err) {
        setError(err instanceof Error ? err.message : "An error occurred");
      } finally {
        setLoading(false);
      }
    };

    fetchNotices();
  }, [user]);

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case "URGENT": return "text-red-700 bg-red-100 border-red-200";
      case "HIGH": return "text-orange-700 bg-orange-100 border-orange-200";
      case "MEDIUM": return "text-blue-700 bg-blue-100 border-blue-200";
      case "LOW": return "text-green-700 bg-green-100 border-green-200";
      default: return "text-gray-700 bg-gray-100 border-gray-200";
    }
  };

  const getPriorityIcon = (priority: string) => {
    switch (priority) {
      case "URGENT": return "üö®";
      case "HIGH": return "‚ö†Ô∏è";
      case "MEDIUM": return "üì¢";
      case "LOW": return "‚ÑπÔ∏è";
      default: return "üìù";
    }
  };

  const isExpired = (expiryDate?: string) => {
    if (!expiryDate) return false;
    return new Date(expiryDate) < new Date();
  };

  const filteredNotices = notices.filter(notice => {
    const matchesPriority = selectedPriority === "ALL" || notice.priority === selectedPriority;
    const matchesSearch = notice.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         notice.content.toLowerCase().includes(searchTerm.toLowerCase());
    return matchesPriority && matchesSearch && notice.isActive;
  });

  const priorityStats = {
    URGENT: notices.filter(n => n.priority === "URGENT" && n.isActive).length,
    HIGH: notices.filter(n => n.priority === "HIGH" && n.isActive).length,
    MEDIUM: notices.filter(n => n.priority === "MEDIUM" && n.isActive).length,
    LOW: notices.filter(n => n.priority === "LOW" && n.isActive).length
  };

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-8"></div>
          <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
            {[...Array(4)].map((_, i) => (
              <div key={i} className="h-32 bg-gray-200 rounded-xl"></div>
            ))}
          </div>
          <div className="space-y-4">
            {[...Array(5)].map((_, i) => (
              <div key={i} className="h-32 bg-gray-200 rounded-xl"></div>
            ))}
          </div>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="p-8">
        <div className="bg-red-50 border border-red-200 rounded-xl p-6">
          <div className="flex items-center">
            <span className="text-2xl mr-3">‚ùå</span>
            <div>
              <h3 className="text-lg font-semibold text-red-800">Error Loading Notices</h3>
              <p className="text-red-600">{error}</p>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="p-8 space-y-8">
      {/* Header */}
      <div className="bg-gradient-to-r from-amber-600 to-orange-600 rounded-2xl p-8 text-white">
        <h1 className="text-3xl font-bold mb-2 flex items-center">
          <span className="text-4xl mr-4">üì¢</span>
          Notice Board
        </h1>
        <p className="text-amber-100 text-lg">
          Stay updated with the latest announcements and important information
        </p>
      </div>

      {/* Priority Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
        <div className="bg-gradient-to-br from-red-500 to-red-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-red-100 text-sm font-medium">Urgent</p>
              <p className="text-2xl font-bold mt-2">{priorityStats.URGENT}</p>
            </div>
            <div className="text-3xl opacity-80">üö®</div>
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-orange-500 to-orange-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-orange-100 text-sm font-medium">High Priority</p>
              <p className="text-2xl font-bold mt-2">{priorityStats.HIGH}</p>
            </div>
            <div className="text-3xl opacity-80">‚ö†Ô∏è</div>
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-blue-500 to-blue-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-blue-100 text-sm font-medium">Medium Priority</p>
              <p className="text-2xl font-bold mt-2">{priorityStats.MEDIUM}</p>
            </div>
            <div className="text-3xl opacity-80">üì¢</div>
          </div>
        </div>
        
        <div className="bg-gradient-to-br from-green-500 to-green-600 rounded-xl p-6 text-white">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-green-100 text-sm font-medium">Low Priority</p>
              <p className="text-2xl font-bold mt-2">{priorityStats.LOW}</p>
            </div>
            <div className="text-3xl opacity-80">‚ÑπÔ∏è</div>
          </div>
        </div>
      </div>

      {/* Filters */}
      <div className="bg-white rounded-xl shadow-lg p-6">
        <div className="flex flex-wrap gap-4 items-center">
          <div className="flex-1 min-w-64">
            <div className="relative">
              <input
                type="text"
                placeholder="Search notices..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-amber-500 focus:border-transparent"
              />
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <span className="text-gray-400">üîç</span>
              </div>
            </div>
          </div>
          <div>
            <select
              value={selectedPriority}
              onChange={(e) => setSelectedPriority(e.target.value)}
              className="border border-gray-300 rounded-lg px-4 py-3 focus:ring-2 focus:ring-amber-500 focus:border-transparent"
            >
              <option value="ALL">All Priorities</option>
              <option value="URGENT">üö® Urgent</option>
              <option value="HIGH">‚ö†Ô∏è High</option>
              <option value="MEDIUM">üì¢ Medium</option>
              <option value="LOW">‚ÑπÔ∏è Low</option>
            </select>
          </div>
        </div>
      </div>

      {/* Notices List */}
      <div className="space-y-6">
        {filteredNotices.map((notice) => (
          <div key={notice.id} className={`bg-white rounded-xl shadow-lg overflow-hidden hover:shadow-xl transition-all duration-300 ${
            isExpired(notice.expiryDate) ? 'opacity-75' : ''
          }`}>
            <div className="p-6">
              <div className="flex items-start justify-between mb-4">
                <div className="flex items-center space-x-3">
                  <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium border ${getPriorityColor(notice.priority)}`}>
                    <span className="mr-2">{getPriorityIcon(notice.priority)}</span>
                    {notice.priority}
                  </span>
                  {isExpired(notice.expiryDate) && (
                    <span className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-gray-100 text-gray-700 border border-gray-200">
                      ‚è∞ Expired
                    </span>
                  )}
                </div>
                <div className="text-sm text-gray-500">
                  {new Date(notice.createdAt).toLocaleDateString('en-US', {
                    year: 'numeric',
                    month: 'short',
                    day: 'numeric'
                  })}
                </div>
              </div>
              
              <h3 className="text-xl font-bold text-gray-900 mb-3">{notice.title}</h3>
              
              <p className="text-gray-700 mb-4 leading-relaxed">{notice.content}</p>
              
              <div className="flex items-center justify-between pt-4 border-t border-gray-200">
                <div className="flex items-center space-x-4">
                  <div className="flex items-center text-sm text-gray-600">
                    <span className="mr-2">üë§</span>
                    <span className="font-medium">{notice.author}</span>
                  </div>
                  <div className="flex items-center text-sm text-gray-600">
                    <span className="mr-2">üë•</span>
                    <span>{notice.targetRoles.join(", ")}</span>
                  </div>
                </div>
                
                {notice.expiryDate && (
                  <div className="flex items-center text-sm text-gray-600">
                    <span className="mr-2">‚è∞</span>
                    <span>
                      Expires: {new Date(notice.expiryDate).toLocaleDateString('en-US', {
                        month: 'short',
                        day: 'numeric',
                        year: 'numeric'
                      })}
                    </span>
                  </div>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>

      {filteredNotices.length === 0 && (
        <div className="bg-white rounded-xl shadow-lg p-12 text-center">
          <div className="text-6xl mb-4">üì¢</div>
          <h3 className="text-lg font-semibold text-gray-900 mb-2">No Notices Found</h3>
          <p className="text-gray-600">
            {searchTerm || selectedPriority !== "ALL" 
              ? "Try adjusting your search or filter criteria."
              : "There are no active notices at the moment."
            }
          </p>
        </div>
      )}

      {/* Quick Actions for Admin/Teachers */}
      {(user?.role === "ADMIN" || user?.role === "SUPER_ADMIN" || user?.role === "TEACHER") && (
        <div className="bg-white rounded-xl shadow-lg p-6">
          <h3 className="text-lg font-bold text-gray-900 mb-4 flex items-center">
            <span className="text-2xl mr-3">‚ö°</span>
            Quick Actions
          </h3>
          <div className="flex flex-wrap gap-4">
            <button className="bg-gradient-to-r from-amber-500 to-orange-500 text-white px-6 py-3 rounded-lg hover:from-amber-600 hover:to-orange-600 transition-all duration-200 transform hover:scale-105 font-medium">
              üìù Create Notice
            </button>
            <button className="bg-gradient-to-r from-blue-500 to-blue-600 text-white px-6 py-3 rounded-lg hover:from-blue-600 hover:to-blue-700 transition-all duration-200 transform hover:scale-105 font-medium">
              üìä View Analytics
            </button>
            <button className="bg-gradient-to-r from-green-500 to-green-600 text-white px-6 py-3 rounded-lg hover:from-green-600 hover:to-green-700 transition-all duration-200 transform hover:scale-105 font-medium">
              üìã Manage Notices
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default NoticesPage;
